.c-page {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.c-page__wrapper {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  width: 375px;
  height: 600px;
  margin: -300px 0 0 -187.5px;
  overflow: hidden;
}



